@import url(https://fonts.googleapis.com/css?family=Alegreya+Sans+SC:900);
@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Redressed&display=swap);
:root {
  --primary: #4CAF50;
  --secondary: #FFC107;
  --light-gray: #DADCE0;
  --dark: #222;
}

body {
  margin: 30px;
}

.btn {
  padding: 10px 20px;
  cursor: pointer;
  min-width: 100px;
  border-radius: 3px;
  color: black;
  transition: opacity 180ms ease-out, transform 180ms ease-out;
  background-color: #ccc5c5;
}
a.btn {
  font-size: 15px;
}
.btn:hover {
  opacity: 0.8;
}
.btn:active {
  transform: translate(1px, 1px);
}
.btn-primary {
  background-color: #4CAF50;
  background-color: var(--primary);
}
.btn-outline {
  border: 1px solid #222;
  border: 1px solid var(--dark);
  color: #222;
  color: var(--dark);
}
ul {
  display: block;
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Code By Webdevtrick (https://webdevtrick.com) */

  .dashboard-cards {
    position: relative;
    padding-bottom: 50px;
    margin: 0 !important;
    left: 10px;
  }

  .dashboard-cards .card {
    background: #ffffff;
    display: inline-block;
    perspective: 1000px;
    z-index: 20;
    padding: 0 !important;
    margin: 5px 5px 10px 5px;
    position: relative;
    text-align: left;
    transition: all 0.3s 0s ease-in;
    z-index: 1;
    width: calc(33.33333333% - 10px);
    cursor: pointer;
    transition: all 0.3s ease;
  }

  .dashboard-cards .card:hover {
    box-shadow: 0 15px 10px -10px rgba(31, 31, 31, 0.5);
    transition: all 0.3s ease;
  }

  .dashboard-cards .card .card-title {
    background: #000000;
    padding: 20px 15px;
    position: relative;
    z-index: 0;
  }

  .dashboard-cards .card .card-title h2 {
    font-size: 24px;
    letter-spacing: -0.05em;
    margin: 0;
    padding: 0;
    color: azure;
  }

  .dashboard-cards .card .card-title h2 small {
    display: block;
    font-size: 14px;
    margin-top: 8px;
    letter-spacing: -0.025em;
    color: azure;
  }

  .dashboard-cards .card .card-description {
    position: relative;
    font-size: 14px;
    border-top: 1px solid #ddd;
    padding: 10px 15px 0 15px;
  }

  .dashboard-cards .card .card-actions {
    box-shadow: 0 2px 0px 0 rgba(0, 0, 0, 0.075);
    padding: 10px;
    text-align: center;
  }

  .dashboard-cards .card .card-flap {
    background: #d9d9d9;
    position: absolute;
    width: 100%;
    transform-origin: top;
    transform: rotateX(-90deg);
  }

  .dashboard-cards .card .flap1 {
    transition: all 0.3s 0.3s ease-out;
    z-index: -1;
  }

  .dashboard-cards .card .flap2 {
    transition: all 0.3s 0s ease-out;
    z-index: -2;
  }

  .dashboard-cards.showing .card {
    cursor: pointer;
    opacity: 0.6;
    transform: scale(0.88);
  }

  .dashboard-cards .no-touch .dashboard-cards.showing .card:hover {
    opacity: 0.94;
    transform: scale(0.92);
  }

  .dashboard-cards .card.d-card-show {
    opacity: 1 !important;
    transform: scale(1) !important;
  }

  .dashboard-cards .card.d-card-show .card-flap {
    background: #ffffff;
    transform: rotateX(0deg);
  }

  .dashboard-cards .card.d-card-show .flap1 {
    transition: all 0.3s 0s ease-out;
  }

  .dashboard-cards .card.d-card-show .flap2 {
    transition: all 0.3s 0.2s ease-out;
  }

  .dashboard-cards .card .task-count {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 22px;
    right: 10px;
    background: #ecf0f1;
    text-align: center;
    line-height: 40px;
    border-radius: 100%;
    color: #333333;
    font-weight: 600;
    transition: all .2s ease;
  }

  .dashboard-cards .task-list {
    padding: 0 !important;
  }

  .dashboard-cards .task-list li {
    padding: 10px 0;
    padding-left: 10px;
    margin: 3px 0;
    list-style-type: none;
    border-bottom: 1px solid #e9ebed;
    border-left: 3px solid #ff3a3a;
    transition: all .2s ease;
  }

  .dashboard-cards .task-list li:hover {
    background: #ecf0f1;
    transition: all .2s ease;
  }

  .dashboard-cards .task-list li span {
    float: right;
    color: #ff3a3a;
    margin-right: 5px;
  }

  .dashboard-cards.showing .card.d-card-show .task-count {
    color: #ffffff;
    background: #ff3a3a;
    transition: all .2s ease;
  }

  .dashboard-cards .card-actions .btn {
    color: #333;
  }

  .dashboard-cards .card-actions .btn:hover {
    color: #ff3a3a;
  }
  @media (max-width:750px) {
    .dashboard-cards .card {
      margin-top: 10px;
      width: 100%;
    }
  }
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }

  body {
    padding: 0;
    font-family: 'Montserrat', serif;
    background-color: #000000a8;
  }

  header {
    padding: 0;
/*     background-size: 800px; */
    height: 200px;
    background: url(data:image/png;base64,);
    margin: 20px;
  }

  #blog {
    font-size: 100px;
    font-family: 'Redressed', cursive;
    color: #0490bb;
    padding: 0;
    margin: 0;
    text-align: center;
    mix-blend-mode: color-burn;
  }

  /* The grid container */
  .grid-container {
    display: grid;
    grid-template-areas:
      'header header header header header header'
      'left left middle middle middle middle'
      'footer footer footer footer footer footer';
    background-image: url(https://www.jandlmarketing.com/wp-content/uploads/2020/01/Sleek-Background-Pattern.jpg);
    background-size: cover;
  }

  .left,
  .middle,
  .right {
    padding: 10px;
  }

  /* Style the left column */
  .left {
    grid-area: left;
    background: url(https://backgroundcheckall.com/wp-content/uploads/2017/12/sleek-background-300x200.jpg);
    mix-blend-mode: difference;
    width: 530px;
  }

  /* Style the middle column */
  .middle {
    grid-area: middle;
  }

  /* Style the right column */
  .right {
    grid-area: right;
  }
  .mid-header {
    color: whitesmoke;
    text-align: center;
    font-family: "Gill Sans", sans-serif;
    font-size: 20px;
  }
  h3.mid-header {
    margin-top: 50px;
  }

  /* Style the footer */
  .footer {
    grid-area: footer;
    background: url(data:image/png;base64,);
    padding: 0;
/*     background-size: 800px; */
    height: 200px;
    margin: 20px;
    background-position: bottom;
  }
  .links {
    position: absolute;
    margin-top: 35px;
  }
  /* Responsive layout - makes the three columns stack on top of each other instead of next to each other */
  @media (max-width: 600px) {
    .grid-container  {
      grid-template-areas:
        'header header header header header header'
        'left left left left left left'
        'middle middle middle middle middle middle'
        'right right right right right right'
        'footer footer footer footer footer footer';
    }
  }
/* Trivia */
main{
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}

.trivia-left {
  grid-area: left;
  width: 30rem;
}

.game-quiz-container{
    width: 51rem;
    height: 40rem;
    background-color: #000000;
    display: table-caption;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border-radius: 30px;
}

.game-details-container{
    height: 4rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    color: whitesmoke;
    margin-left: 35px;
}

.game-details-container h1{
    font-size: 1.7rem;
}

.game-question-container{
    height: 8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #f45d22;
    border-radius: 25px;
    margin-top: 15px;
}

.game-question-container h1{
    font-size: 1.5rem;
    text-align: center;
    padding: 3px;
    color: whitesmoke;
    text-overflow: clip;
}


.game-options-container{
    height: 12rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    margin-top: 20px;
}

.game-options-container span{
    width: 49%;
    height: 7rem;
    border: 2px solid #001fff;
    border-radius: 20px;
    overflow: hidden;
}
span label{
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: transform 0.3s;
    font-weight: 400;
    color: whitesmoke;
    font-size: 1.5em;
}

span label:hover{
    transform: scale(1.12);
    color: white;
}

input[type="radio"] {
    position: relative;
    display: none;
}


input[type=radio]:checked ~ .option {
    background: paleturquoise;
}

.next-button-container{
    height: 3rem;
    display: flex;
    justify-content: center;
}
.next-button-container button{
    width: 12rem;
    height: 3rem;
    border-radius: 10px;
    background: none;
    color: whitesmoke;
    font-weight: 500;
    border: 2px solid #f45d22;
    cursor: pointer;
    outline: none;
    justify-content: center;
    font-size: 15px;
    margin-top: 70px;

}
.next-button-container button:hover{
    background-color: rgb(143, 93, 93);
}

.modal-container{
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    animation: fadeIn 1.2s ease-in-out;
}

.modal-content-container{
    height: 20rem;
    width: 25rem;
    background-color: rgb(43, 42, 42);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    border-radius: 25px;
}

.modal-content-container h1{
    font-size: 1.3rem;
    height: 3rem;
    color: lightgray;
    text-align: center;
}

.grade-details{
    width: 15rem;
    height: 10rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.grade-details p{
    color: white;
    text-align: center;
}

.modal-button-container{
    height: 2rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal-button-container button{
    width: 10rem;
    height: 2rem;
    background: none;
    outline: none;
    border: 1px solid rgb(252, 242, 241);
    color: white;
    font-size: 1.1rem;
    cursor: pointer;
    border-radius: 20px;
}
.modal-button-container button:hover{
    background-color: rgb(83, 82, 82);
}
#play-button {
    width: 12rem;
    height: 3.5rem;
    border-radius: 10px;
    background: none;
    color: whitesmoke;
    font-weight: 450;
    border: 2px solid #0bd104;
    cursor: pointer;
    outline: none;
    font-size: 15px;
}

@media(min-width : 300px) and (max-width : 350px){
    .game-quiz-container{
        width: 90%;
        height: 80vh;
     }
     .game-details-container h1{
        font-size: 0.8rem;
     }

     .game-question-container{
        height: 6rem;
     }
     .game-question-container h1{
       font-size: 0.9rem;
    }

    .game-options-container span{
        width: 90%;
        height: 2.5rem;
    }
    .game-options-container span label{
        font-size: 0.8rem;
    }
    .modal-content-container{
        width: 90%;
        height: 25rem;
    }

    .modal-content-container h1{
        font-size: 1.2rem;
    }
}

@media(min-width : 350px) and (max-width : 700px){
   .game-quiz-container{
       width: 90%;
       height: 80vh;
    }
    .game-details-container h1{
        font-size: 1rem;
    }

    .game-question-container{
        height: 8rem;
    }

    .game-question-container h1{
        font-size: 0.9rem;
     }

    .game-options-container span{
        width: 90%;
    }
    .modal-content-container{
        width: 90%;
        height: 25rem;
    }
    .modal-content-container h1{
        font-size: 1.2rem;
    }
}



